import React,{useState,useEffect} from 'react'
import {Carousel,Button} from 'react-bootstrap'
import FourthwallImage from '../img/Fourthwall.png'
import zdenardiImage from '../img/zdenardiThumb.png'
import githubimage from '../img/githubthumb.png'

const Carousels = () =>{
    const [toggleAni,setToggleAni] = useState(false);
    const [windowSize,setWindowSize] = useState({
        width:undefined,
        height:undefined,
    })
    
    const viewSize = windowSize;
    //custom hook for window size
  

    useEffect(()=>{
        function handleResize(){
            setWindowSize({
                width:window.innerWidth,
                height:window.innerHeight,
            });
        }

        //add event listener
        window.addEventListener('resize',handleResize);
        handleResize();


        // cleanup
        return () => window.removeEventListener("resize",handleResize)


    },[])




const CustomCaption = ({title,text,btnText="SEE IT LIVE",btnURL}) =>{
    


    function styleFixer(){
        let captionStyle={}
        if(windowSize.width < 708){
        captionStyle={
            borderWidth: "10px",
            borderColor: "#cb7e75",
            borderStyle: "solid",
            backgroundColor:"#50459b",
            color:"white",
            width:"100",
            height:"75%",
            paddingBottom:"0",
            fontSize:"75%"
            
         }
        }else{
            captionStyle={
            borderWidth: "10px",
            borderColor: "#cb7e75",
            borderStyle: "solid",
            backgroundColor:"#50459b",
            color:"white",
            margin:"auto",
            width:"50%",
            }
        }

        return (captionStyle)
    }
    styleFixer();
    return(
        <Carousel.Caption 
        onMouseEnter={()=>{setToggleAni(!toggleAni)}}
        onMouseLeave={()=>{setToggleAni(!toggleAni)}}
        style={styleFixer()}
            className={toggleAni ? "animate__animated animate__pulse":""}>
        <div>
        
        <h3 className="projectTitle">{title}</h3>
        <p className="projectP">{text}</p>
        <Button
        style={{
            backgroundColor:"#887ecb",
            borderColor:"#cb7e75",
            fontFamily:"commodore_64_angledregular",
            color:"white"}}
        onClick={()=> window.open(`${btnURL}`)}
        className="projectButton"
        >
            * {btnText} *
        </Button>
        </div>
        </Carousel.Caption>
        

    )
}


    return(
        
        <>
        <div className="main-content container">
        <h1 className="divider">Live Projects</h1>
        <Carousel> 

        <Carousel.Item
            style={{maxHeight:"30vh"}}>
                <img
                className=" d-block mx-auto"
                src={FourthwallImage}
                alt="Fourth Wall"
                />
            <CustomCaption title="Fourthwall Reviews" text="React Frontend, NodesJS/Express Backend connected to MYSQL" btnURL="https://fourthwallreviews.com"/>
            </Carousel.Item >

            {/* <Carousel.Item style={{maxHeight:"30vh"}}>
                <img
                className=" d-block mx-auto"
                src={zdenardiImage}
                alt="zdenardi.com thumb"
                />

                <CustomCaption title="Single Page Personal Website" text="React Frontend, Node/Express backend connected to MongoDB" btnURL="http://zdenardi.com"/>
            </Carousel.Item>

            <Carousel.Item style={{maxHeight:"30vh"}}>
                <img
                className=" d-block mx-auto"
                src={githubimage}
                alt="github blurred image"
                />
                <CustomCaption title="See more at my github" text="Projects with React, Ruby on Rails, PHP and more" btnText="Check it out" btnURL = "https://github.com/zdenardi"/>
            </Carousel.Item> */}
    </Carousel>
    </div>
        </>
    )
}

export default Carousels